.container {
    /* max-height: 545px; */
    width: 940px;
    border: 1px #DDDDDD solid;
    border-radius: 4px;
    /* overflow-y: scroll;
    overflow-x: hidden; */
}

.container ul {
    margin: 20px;
}

.container li {
    border-radius: 0px;
    border-left: none;
    border-right: none;
}

.container li * {
    cursor: pointer;
}

.container li a {
    /* color: #2A878C; */
    color: grey;
    float: left;
    width: 90%;
    font-size: .90rem;
    margin-top: 5px;

}


.container li:first-child {
    border-top: none;
}

.container li:last-child {
    border-bottom: none;
}


